html {
  font-size: 16px;
}
.main-page {
  padding: 0.0625rem 1.875rem 1.25rem 1.875rem;
  height: 100%;
  overflow: auto;
  position: relative;
}
.main-page.img {
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 40%;
}
.main-page header .page-title {
  color: #0F296D;
  font-family: 'MyCustomFont';
  text-align: left;
  font-size: 3rem;
  font-weight: 600;
  margin: 0 0 0.625rem 0;
}
.main-card {
  background-color: rgba(186, 222, 162, 0.264);
  padding: 1.25rem;
  border-radius: 0.5rem;
}
.subtitle {
  color: #0F296D;
  font-family: 'MyCustomFont';
  font-weight: 600;
  text-align: left;
  font-size: 1.875rem;
}
.info-list {
  color: #333;
  text-align: left;
}
.info-list .info-item {
  margin-bottom: 1.25rem;
}
.info-details {
  font-size: 1.25rem;
  font-family: 'MyCustomFont';
}
.description-list {
  list-style-type: disc;
  margin-left: 1.25rem;
  font-size: 1.25rem;
  font-family: 'MyCustomFont';
}
.main-btn {
  padding: 0.625rem;
  font-size: 1.875rem;
  font-family: 'MyCustomFont';
  background-color: #0F296D;
  color: #fff;
  min-width: 7.5rem;
  text-align: center;
}
.size-btn {
  font-size: 1.875rem;
  color: #0F296D;
  font-family: 'MyCustomFont';
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.size-btn:hover {
  transform: scale(1.3);
}
.zigzag-card {
  width: 70%;
}
.zigzag-card:nth-child(even) {
  float: right;
}
.addiction-prevention.main-page .main-card {
  margin: 0 auto;
  padding: 20px;
  background-color: #FFF8E7;
}
.addiction-prevention.main-page .page-title {
  font-size: 2rem;
  color: #000000 !important;
  margin-bottom: 1rem;
}
@media (max-width: 768px) {
  .addiction-prevention.main-page .page-title {
    font-size: 1.5rem;
  }
}
.addiction-prevention.main-page .subtitle {
  font-size: 1.5rem;
  color: #000000;
}
@media (max-width: 768px) {
  .addiction-prevention.main-page .subtitle {
    font-size: 1.25rem;
  }
}
.addiction-prevention.main-page .info-details {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 1rem;
}
@media (max-width: 768px) {
  .addiction-prevention.main-page .info-details {
    font-size: 0.9rem;
  }
}
.addiction-prevention.main-page .main-btn {
  display: block;
  margin: 1.5rem auto 0;
  font-size: 1.2rem;
  padding: 10px 20px;
  background-color: #0F296D;
  color: white;
  line-height: 1;
  text-align: center;
}
@media (max-width: 768px) {
  .addiction-prevention.main-page .main-btn {
    font-size: 1rem;
    padding: 8px 16px;
  }
}
